<template>
  <div class="select-group">
    <label v-if="label" class="label">{{ label }}</label>
    <div class="select">
      <VSelect
        :value="value"
        :placeholder="placeholder"
        :clearSearchOnSelect="true"
        :searchable="true"
        :options="options"
        :disabled="disabled"
        :label="displayField"
        v-model="selectedOption"
        @input="updateValue"
      ></VSelect>
    </div>
  </div>
</template>

<script>
export default {
  name: "SelectBox",
  props: {
    label: {
      type: String
    },
    placeholder: {
      type: String
    },
    value: {
      type: String
    },
    options: {
      type: Array
    },
    disabled: {
      type: Boolean,
      default: false
    },
    displayField: {
      type: String
    }
  },
  data: () => ({
    selectedOption: null
  }),
  methods: {
    updateValue: function(selectedOption) {
      this.$emit("valuechange", selectedOption);
    }
  }
};
</script>
